.button-handler{
    border: none;
    outline: none;
    width: 74px;
    min-height: 30px;
    border-radius: 8px;
    &__row{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 0 10px;
        padding: 7px 8px;
    }
    &__button{
        cursor: pointer;
    }
}