.input{

    height: 30px;
    width: 100%;
    input{
        width: 100%;
        height: 100%;
        outline: none;
        background: #FFFFFF;
        border: 1px solid #F2F2F3;
        border-radius: 8px;
        padding: 0 12px;


        font-family: 'Nunito';
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 130%;
        color: #B1B1B1;
    }
}

.error{
    border-color: red!important;
}


@media screen and (min-width:1024px) {
.input{

    height: 40px;
    input{
        font-size: 12px;
        transition: all 300ms;
        &:focus{
            border: 1px solid #FFAB08;
            color: #000000;
        }
    }
    
}
}